html {
    overflow: hidden;
}

body {
    margin: 0;
    background-color: rgb(1, 1, 20);
    user-select: none;
}

.canvas-parent {
    text-align: center;
}

.canvas {
    position: absolute;
    height: 100vh;
    transform: translateX(-50%);
    border-left: solid 800px rgb(1, 1, 20); /* Only way to hide clouds from exiting out from behind main-image was to give main-image a border, since it's dynamic positioning/sizing made it impossible to flank it with similarly dynamic divs that would fit flush against its left and right sides */
    border-right: solid 800px rgb(1, 1, 20);
}

.clouds {
    width: 100%;
    height: 100vh;
    position: absolute;
}

.performanceButton {
    margin: 0;
    opacity: .35;
    border-radius: 50%;
}

.performanceButtonContainer {
    position: absolute;
    bottom: 0px;
    right: 0px;
    cursor: pointer;
}

.ripple {
    position: absolute;
    border-radius: 50%;
    animation: rippleEffect .41s cubic-bezier(0, 0, 0, 1); /* ripple active time is 200ms and time of animation needs to be just a bit longer than that otherwise there'll be an animation glitch */
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 1px;
  background: rgba(32,32,1,.5);
  outline: none;
  -webkit-transition: .2s;
  transition: opacity .2s;
}
.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 28px;
  height: 14px;
  border-radius: 25%;
  background: rgba(32,32,1,.85);
  cursor: pointer;
}
.slider::-moz-range-thumb {
  width: 28px;
  height: 14px;
  border-radius: 25%;
  background: rgba(32,32,1,.85);
  cursor: pointer;
}

.title:hover {
    transform: scale(1.2);
    cursor: pointer;
    /* For some reason, placing  " transition: 'all .25s' "  here in .title:hover  gets the transition in but not out */
}

.formContainer {
    visibility: hidden;
    position: absolute;
    top: 50%;
    transform: translateY(-50%) translateX(0%) scaleX(.7);
    transform-origin: right;
    opacity: 0;
    transition: all .3s ease;
}
.formContainerRevealed {
    visibility: visible;
    transform: translateY(-50%) translateX(-20%) scaleX(1);
    opacity: 1;
    transition: all .3s ease;
}
.formSendAnimation {
    transform: translateY(-120%) translateX(-250%) skew(-.06turn, -35deg) scale(.1);
    opacity: 0;
    transition: all 1.5s cubic-bezier(.4,0,1,.9) 1s;
}
.formFields {
    font-size: 19px;
    font-family: 'Cormorant Garamond', serif;
    margin: 1px;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    background-color: rgba(255,255,255,.85);
    transition: all .3s ease;
}
.formFields::placeholder {
    color: rgba(0,0,0,.3);
}
.formFields:hover {
    background-color: rgba(228,228,255,.95);
}

.formFields.message::placeholder {
    font-style: italic;
}

.button {
    font-size: 16px;
    background-color: rgba(0,170,170,.95);
}
.formFields.button:hover {
    background-color: rgba(245,245,215,.85);
}

.buttonText {
    font-family: 'Nothing You Could Do', cursive;
    color: rgba(255,255,255,1);
    transition: all .3s ease;
}
.formFields.button:hover .buttonText {
    color: rgba(1,1,40,1);
    font-weight: 800;
}


/* Would've had to import a seperate library like "glamorous" to have been able to pass pseudo selectors as inline styles (in AudioPlayer.js, which I would have used to make the slider thumb below scale with canvasHeight). Also important to note that I wouldn't want slider thumb to scale very much as it would've be grabbable/useable. Against one of my goals for this project, to make all rendering fluidly dynamic without use of media breakpoints, in this particular case it was the optimal solution. Incidentally, when I decided I wanted to tether color and opacity of the audio interface with veilOpacity, I did import the emotion library and for some reason, though fully integrated without its syntax crashing anything, it didn't work. */
@media only screen and (max-height: 400px) {
  .slider::-webkit-slider-thumb {
    width: 20px;
    height: 10px;
  }
}
@media only screen and (max-height: 400px) {
  .slider::-moz-range-thumb {
      width: 20px;
      height: 10px;
  }
}


@keyframes rippleEffect {
    0% {transform: scale(.1); opacity: .3}
    100% {transform: scale(1); opacity: 0}
}

